body {
  background-color: #101010;
  font-size: 16px;
  height: calc(100% - 54px);
}

#root {
  height: 100%;
}

#root > div {
  height: 100%
}

nav {
  margin-bottom: 1em;
}

.main {
  display: flex;
  background-color: #fff;
  border-radius: 4px;
  margin: 24px 48px;
  padding: 24px;
  flex-direction: row;
  height: calc(100% - 40px)
}

.sidebar {
  flex: 0 0 200px;
  max-width: 200px;
  min-width: 200px;
  width: 200px;
}

.content {
  padding-left: 2em;
}

/* List of check results for a given release */
.dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
  grid-gap: 20px;
}

.card-header {
  font-weight: bold;
}

.card-link {
  color: #212529;
}

.card-link a:hover {
  color: #212529;
}

footer {
  background-color: #101010;
  color: #ccc;
  bottom: 0;
  height: 24px;
  line-height: 15px;
  position: relative;
  text-align: center;
  width: 100%;
}

footer a {
  color: #9cb2dc;
}

@media (max-width: 992px) {
  .user {
    right: 5px;
